<template><undefined>
<h1>Radio 单选框</h1>
<p>单选框</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>用于在多个备选项中选中单个状态。和 Select 的区别是：Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-radio v-model=\&quot;radio\&quot; label=\&quot;first\&quot; disabled&gt;fist&lt;/v-radio&gt;\n    &lt;v-radio v-model=\&quot;radio\&quot; label=\&quot;second\&quot;&gt;second&lt;/v-radio&gt;\n    &lt;v-radio v-model=\&quot;radio\&quot; label=\&quot;third\&quot;&gt;third&lt;/v-radio&gt;\n    &lt;br&gt;\n    &lt;br&gt;\n    &lt;span&gt;选择的值为：{{radio}}&lt;/span&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data: function () {\n        return {\n          radio : 'third'\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-radio v-model="radio" label="first" disabled>fist</v-radio>
    <v-radio v-model="radio" label="second">second</v-radio>
    <v-radio v-model="radio" label="third">third</v-radio>
    <br>
    <br>
    <span>选择的值为：{{radio}}</span>
</template>

</div>
                  <div slot="description"><h4 id="radio"><a class="header-anchor" href="#radio" aria-hidden="true">¶</a> radio</h4>
<p>radio组合使用(不建议单个使用，单个请用checkbox)</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"first"</span> <span class="hljs-attr">disabled</span>&gt;</span>fist<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"second"</span>&gt;</span>second<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"third"</span>&gt;</span>third<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>选择的值为：<span>{{</span>radio<span>}}</span><span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
