    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"apple"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"apple"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>Apple<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"android"</span> &gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"android"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>Android<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"rocket"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">v-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"rocket"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-icon</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>Rocket<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>选中的值为：<span>{{</span>radioValue<span>}}</span><span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">v-button</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"test"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"changeRadioValue"</span>&gt;</span>设置raido的值<span class="hljs-tag">&lt;/<span class="hljs-name">v-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>:{
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">radioValue</span>: <span class="hljs-string">'apple'</span>,
        }
    },
    <span class="hljs-attr">methods</span>: {
        change(selectValue) {
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'radio checked:'</span> + selectValue)
        },
        changeRadioValue(){
            <span class="hljs-keyword">this</span>.radioValue = <span class="hljs-string">'android'</span>
        }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
